.memberships {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .memberships-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
  }
  .memberships-section-title {
    font-size: 24px;
    font-weight: 600;
  }
  .memberships-section-title1 {
    font-size: 18px;
    font-weight: 600;
  }
  .memberships-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
  }
  @media only screen and (max-width: 800px) {
    .memberships-list {
      max-width: 100%;
      justify-content: center;
    }
    .memberships-section {
      flex-direction: column;
      align-items: center;
    }
  }
  